<template>
    <div class="input">
        <label>
            <input
                    :type="type"
                    :value="value"
                    :placeholder="placeholder"
                    @input="onInput($event)"
            />
        </label>
    </div>
</template>

<script>
    export default {
        model: {
            prop: 'value',
            event: 'input'
        },
        props: {
            value: {
                type: String
            },
            type: {
                type: String
            },
            placeholder: {
                type: String
            }
        },
        methods: {
            onInput (event) {
                this.$emit('input',  event.target.value)
            },
        }
    }
</script>

<style scoped>
    input {
        border: none;
        outline: none;
        font-size: 1em;
    }
</style>