import router from '../routes/route'

export function getId() {
    return {userId: localStorage.getItem('userId')}
}

function deleteId() {
    localStorage.removeItem('userId')
}

export function setId(userId) {
    localStorage.setItem('userId', userId)
}

export function apiFetchFactory({fetch}) {
    return async function apiFetch(method, url, body, {
        contentType = 'application/json',
        responseType = 'json',
    } = {}) {
        if (getId()) {
            const res = await fetch(url, {
                method,
                body: JSON.stringify(body),
                headers: {
                    'content-type': contentType,
                },
            })
            if (responseType == 'json') {
                return res
            }
        } else {
            router.push('login')
        }
    }
}

export const apiFetch = apiFetchFactory({fetch})

export const destinationURL = 'http://localhost:3030'